'use strict';

import * as gulp from 'gulp';
import { GulpTask } from '@microsoft/gulp-core-build';
import deployToAzure from './uploadCDN/deployToAzure';

export interface IDeployAzureTaskConfigBase {
  /**
   * The Azure account name. Defaults to undefined.
   */
  account?: string;

  /**
   * The Azure container to use. Defaults to undefined.
   */
  container?: string;

  /**
   * The primary or secondary Azure blob storage access key. Defaults to undefined.
   */
  accessKey?: string;
}

/**
 * Configuration for task which uploads scripts to an Azure blob storage
 */
export interface IDeployAzureStorageTaskConfig extends IDeployAzureTaskConfigBase {
  /**
   * The directory to upload to the CDN. Defaults to the distFolder.
   */
  workingDir?: string;

  /**
   * The glob to search for files in workingDir to upload.
   */
  uploadPath?: string;
}

export class DeployAzureStorageTask extends GulpTask<IDeployAzureStorageTaskConfig> {
  public name: string = 'deploy-azure';

  public taskConfig: IDeployAzureStorageTaskConfig = {
    uploadPath: '**/*.*'
  };

  public executeTask(gulp: gulp.Gulp, completeCallback: () => void): Promise<any> { // tslint:disable-line:no-any
    if (!this.taskConfig.workingDir) {
      this.taskConfig.workingDir = this.buildConfig.distFolder;
    }

    return deployToAzure(this.taskConfig,
                         this.log.bind(this),
                         this.logError.bind(this)).then(completeCallback, (error: Error) => {
      this.logError(error.message);
      if (error.stack) {
        this.logError(error.stack);
      }
    });
  }
}
